import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {WelcomePageComponent} from 'src/app/welcome-page/welcome-page.component';
import { MainSiteContainerComponent } from './main-site/main-site-container/main-site-container.component';
import { HomeComponent } from './main-site/home/home.component';
import { ArtworksComponent } from './main-site/artworks/artworks.component';

const routes: Routes = [
  {path:'', component: WelcomePageComponent},
  {path:'home', component: MainSiteContainerComponent, children : [
    {path: '', component: HomeComponent, pathMatch: 'full'},
    {path:'artworks', component: ArtworksComponent, pathMatch: 'full'}
  ]}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
