import { Component, OnInit } from '@angular/core';
import { ArtworkData } from 'src/app/app-data/artwork-data';
import {Artwork} from 'src/app/interfaces/artwork-interface';
import { ArtworkModel } from 'src/app/models/artwork-model';
@Component({
  selector: 'app-artworks',
  templateUrl: './artworks.component.html',
  styleUrls: ['./artworks.component.scss']
})
export class ArtworksComponent implements OnInit {

  artworkData: ArtworkData = new ArtworkData();
  artworks : Artwork[] = this.artworkData.ArtworkList;
  modalOpen: boolean = false;

  selectedArtwork: Artwork = new ArtworkModel();

  
  constructor() {

  }



  ngOnInit(): void {
  }

  openArtwork(artwork :Artwork): void{
    this.modalOpen = true;

    this.selectedArtwork = artwork;

    setTimeout(()=> this.addShowClassOnModal(), 500);
  }

  closeModal(): void{
    this.modalOpen = false;
    this.selectedArtwork = new ArtworkModel();

    this.removeShowClassOnModal();
  }

  addShowClassOnModal(): void{
    let modalElem: any = document.getElementsByClassName('artwork-modal');        
    modalElem[0].classList.add('show');
  }

  removeShowClassOnModal(): void{
    let modalElem: any = document.getElementsByClassName('artwork-modal');  
    modalElem[0].classList.remove('show');
  }

}
