import {Artwork} from 'src/app/interfaces/artwork-interface';

export class ArtworkModel implements Artwork{

    Name: string;
    Url: string;
    Title: string;
    Description: string;
    Width: number;
    Height: number;
    Orientation: string;
    CreatedDate: Date|null;


    constructor(){
        this.Name = "";
        this.Title = "";
        this.Url = "";
        this.Description = "";
        this.Width = 0;
        this.Height = 0;
        this.Orientation = "";
        this.CreatedDate = null;
    }
}